import { isArray, isObject, isString } from '../../utils'

const supportRules = {
  required: { required: true, message: '', trigger: 'blur' },
  string: { type: 'string', message: '', trigger: 'blur' },
  number: { type: 'number', message: '', trigger: 'blur' },
  boolean: { type: 'boolean', message: '', trigger: 'blur' },
  integer: { type: 'integer', message: '', trigger: 'blur' },
  float: { type: 'float', message: '', trigger: 'blur' },
  array: { type: 'array', message: '', trigger: 'blur' },
  enum: (enumData) => {
    return { type: 'enum', enum: enumData.split(','), message: '' + enumData + '', trigger: 'change' }
  },
  url: { type: 'url', message: `url`, trigger: 'change' },
  email: { type: 'email', message: ``, trigger: 'change' },
  regex: (pattern) => {
    return { pattern: new RegExp(pattern), message: '' + pattern, trigger: 'blur' }
  },
  len: (length) => {
    return { len: parseInt(length), message: `${length}`, trigger: 'blur' }
  },
  min: (length) => {
    return { min: parseInt(length), message: `${length}`, trigger: 'blur' }
  },
  max: (length) => {
    return { max: parseInt(length), message: `${length}`, trigger: 'blur' }
  },
  required_if: (field, formData, ref) => {
    return { validator: function(rule, value, callback) {
      if (formData[field] !== undefined && String(value).length === 0) {
        callback('')
      } else {
        callback()
      }
    }, trigger: 'blur' }
  }
}

export default function transRule(rules, formData, ref) {
  if (isArray(rules)) {
    return rules
  }
  if (isObject(rules)) {
    return [rules]
  }
  if (isString(rules)) {
    const parts = rules.split('|')
    const tmp = []
    parts.forEach(item => {
      if (item.indexOf(':') > -1) {
        const tokens = item.split(':')
        const name = tokens[0]
        const args = tokens[1]
        if (supportRules[name] !== undefined) {
          tmp.push(supportRules[name](args, formData, ref))
        } else {
          console.warn('not support rule: ' + name)
        }
      } else {
        if (supportRules[item] !== undefined) {
          tmp.push(supportRules[item])
        } else {
          console.warn('not support rule: ' + item)
        }
      }
    })

    return tmp
  }
  return []
}
